.App {
  text-align: center;
  margin:auto;
  margin-top: 75px;
  height: 75vh;
  width: 75%;
}

h1 { 
	text-align: center;
	text-transform: uppercase;
	letter-spacing: 3px;
	font-weight: 700;
	font-size: 30px;
}

body {
  margin: 0;
  font-family: 'Roboto', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background: #C9FFBF;  
	background: -webkit-linear-gradient(to right, #284b79, #C9FFBF);  
	background: linear-gradient(to right, #74b0d3, #C9FFBF); 
}

code {
  font-family: 'Roboto', sans-serif;
}